        "paragraphs": "[\"合肥是安徽省省会，长三角城市群副中心，面积11445平方公里，人口796.5万人，2017年地区生产总值7213亿元。合肥是综合性国家科学中心和国家集成电路产业中心，拥有两院院士108人、国家“千人计划”专家274人，高等院校60所。\",\"作为“一带一路”和“长江经济带”交汇的双节点城市，合肥区域是华夏幸福业务拓展的重点之一。以合肥为核心，华夏幸福已在舒城、长丰、肥东等地实现布局，打造产业新城和产业小镇。\"]",
