                "industryColony": "{\"title\":\"产业集群\",\"paragraphs\":[\"华夏幸福坚持“产业优先”的核心策略，在舒城杭埠产业新城重点发展新型显示、智能终端、新能源汽车及核心零部件、智能制造装备四大产业集群\"],\"sections\":[{\"icon\":\"http://static.viicb.com/city/jinrongshangmao.png\",\"title\":\"新型显示\",\"paragraphs\":\"重点发展触摸屏、液晶模组、触控模组、PMOLED等中小尺寸面板，同时引入上游材料等配套环节，打造全国知名的新型显示产业集群。\"},{\"icon\":\"http://static.viicb.com/city/zhinengzhongduan.png\",\"title\":\"智能终端\",\"paragraphs\":\"重点发展智能消费电子、可穿戴设备、智能硬件，打造一流的智能终端产业集群。\"},{\"icon\":\"http://static.viicb.com/city/xinnengyuanqiche.png\",\"title\":\"新能源汽车及核心零部件\",\"paragraphs\":\"重点发展新能源汽车电池、电机、电控等核心零部件，同时引入新能源整车，与新站高新区、合肥高新区和包河经开区新能源汽车产业集群协同发展，打造配套完善的新能源汽车及核心零部件产业集群。\"},{\"icon\":\"http://static.viicb.com/city/zhinengzhongduan.png\",\"title\":\"智能制造装备\",\"paragraphs\":\"重点发展精密加工件、结构件、高端精密部件、工业自动化、数控机床等设备，打造智能制造装备产业集群。\"}]}"
