                "industryColony": "{\"title\":\"产业集群\",\"paragraphs\":[\"华夏幸福坚持“产业优先”的核心策略，在长丰产业新城重点发展新型显示、应用材料、汽车及零部件三大产业集群。\"],\"sections\":[{\"icon\":\"http://static.viicb.com/city/jinrongshangmao.png\",\"title\":\"新型显示\",\"paragraphs\":\"重点发展模组制造等核心环节，加速龙头导入，引入上游材料等配套环节,打造全国知名的新型显示产业集群。\"},{\"icon\":\"http://static.viicb.com/city/xincailiao.png\",\"title\":\"应用材料\",\"paragraphs\":\"积极发展汽车应用材料和高端装备应用材料，打造面向长三角产业升级的应用材料集群。\"},{\"icon\":\"http://static.viicb.com/city/xinnengyuanqiche.png\",\"title\":\"汽车及零部件\",\"paragraphs\":\"积极发展汽车电子等高附加值汽车零部件，打造产业配套完善、与周边汽车产业协同发展的汽车及零部件产业集群。\"}]}"
