                "industryEnv": "{\"title\":\"产业环境\",\"banners\":[],\"paragraphs\":[\"金安应用材料产业面临重要机遇。长三角应用材料需求极大，材料供给沿长江经济带向西转移，六安金安已聚集大批新材料企业，具备承接产业转移基础。\",\"合肥家电产业和电子信息产业发达，助力六安金安发展智能终端产业。合肥已聚集海尔、美的、长虹、格力、晶弘、美菱、三洋、惠而浦、TCL、万和等知名企业，家电、电子信息两大千亿级产业为六安金安发展智能终端产业创造良好条件。\",\"六安金安具备发展工业智能控制的区位优势。金安紧邻的合肥是国家“中国制造2025”试点示范城市，汽车、工程机械、机床等产业高速成长，形成向周边外溢辐射态势。\",\"金安紧邻合肥，位于物产丰富的粮食主产区。周边城镇食品加工及商贸流通非常发达，紧邻长三角3亿人消费大市场，承接合肥、长三角城市的食品产业溢出，将发展成为长三角的“后厨”。\"]}",
                "industryColony": "{\"title\":\"产业集群\",\"paragraphs\":[\"华夏幸福坚持“产业优先”的核心策略，在六安金安产业新城重点发展应用材料、智能终端、工业智能控制、现代食品四大产业集群。\"],\"sections\":[{\"icon\":\"http://static.viicb.com/city/jinrongshangmao.png\",\"title\":\"应用材料\",\"paragraphs\":\"重点发展高端装备材料、包装新材料和新型材料等产品，打造面向长三角产业升级的应用材料产业集群。\"},{\"icon\":\"http://static.viicb.com/city/jinrongshangmao.png\",\"title\":\"智能终端\",\"paragraphs\":\"重点发展电子元器件、智能家居、智能安防、智能硬件等产品，打造一流的智能终端产业集群。\"},{\"icon\":\"http://static.viicb.com/city/jinrongshangmao.png\",\"title\":\"工业智能控制\",\"paragraphs\":\"重点发展机器视觉、传感器、仪器仪表、变频器、电气设备等产业，打造较大规模的工业智能控制产业集群。\"},{\"icon\":\"http://static.viicb.com/city/jinrongshangmao.png\",\"title\":\"现代食品\",\"paragraphs\":\"重点发展休闲食品和功能性食品产业，打造绿色健康的现代食品产业集群。\"}]}"
